import store from '../store.js'
import { MessageBox,Toast,Indicator } from 'mint-ui'
import banner2 from '../assets/banner1_03.jpg'
import banner1 from '../assets/banner2_03.jpg'
import list1 from '../assets/list1_03.jpg'
import list2 from '../assets/list2_06.jpg'
import list3 from '../assets/list3_08.jpg'

export default {
  name: 'app',
  data () {
    return {
      loadIndex:0,
      index:0,
      num:0,
      demo01_list:[{
          url: 'javascript:',
          img: banner1,
        }, {
          url: 'javascript:',
          img: banner2,
      }],
      listIndex:[list1,list2,list3]
    }
  },
  methods: {
    numFun(){
      let that =this;
      let timer = setInterval(function(){
        that.num++;
        console.log(that.num);
        if(that.num >=100){
          that.num =100;
          clearInterval(timer);
          timer = null;
          setTimeout(function(){
            that.loadIndex=1;
          },1000);
        }
      },50);
    },
    toCon(e){
      store.commit('infoFun1',e);
      this.$router.push({
        name:'content'
      })
    },
    swiperFun(){
      let that = this;
      if(this.index==0){
        setTimeout(function(){
          sessionStorage.firstFlag=1;
          that.loadIndex=2
        },700);

      }
    }

  },
  created:function(){
    if(sessionStorage.firstFlag==1){
      this.loadIndex=2
    }else {
      if(this.loadIndex==0){
        this.numFun();
      }
    }

    Indicator.close();

  }
}



