import { MessageBox,Toast,Indicator } from 'mint-ui'

export default {
  prop:['message'],
  name: 'AppFooter',
  data () {
    return {
      nav2:'/login',
      nav3:true,
      nav5:'/detail'
    }
  },
  methods:{
    nav3Fun(){
      if(this.nav3){
        // Indicator.open('jia');
        this.$router.push({
          name:'result'
        })
      }else{
        this.$router.push({
          name:'list'
        })
      }
    }
  },
  created:function(){
    if((location.hash.indexOf('info')<0)){
      this.nav3=false
    };
  }
}



